.design {
  display: flex;
  justify-content: center;
}

.fonts {
  font-family: "Reggae One", cursive;
}

#modal-box {
  width: 500px;
  min-height: auto;
  position: absolute;
  top: 15%;
  left: 30%;
  border: 1px solid rgb(72, 0, 255);
  background: rgb(247, 242, 204);
  box-sizing: border-box;
  padding-left: 5px;
  border-radius: 10px;
  box-shadow: 0 4px 8px 0 rgba(0, 0, 0, 0.2), 0 6px 20px 0 rgba(0, 0, 0, 0.19);
  animation-name: animatetop;
  animation-duration: 0.6s;
  /* display: none; */
}
.close {
  display: block;
  display: flex;
  justify-content: flex-end;
  box-sizing: border-box;
}
#close-btn {
  padding: 1px 3px;
  font-size: 18px;
  border-radius: 5px;
  background: red;
  color: white;
  cursor: pointer;
}
#close-btn:hover {
  background: rgb(155, 248, 112);
  color: black;
}
#modal-heading {
  float: left;
}
#modal-close {
  float: right;
  width: 20px;
  height: 20px;
  border-radius: 50%;
  display: flex;
  align-items: center;
  justify-content: center;
  box-sizing: border-box;
  border: 1px solid black;
  color: white;
  background: red;
  font-size: 18px;
  cursor: pointer;
}
#modal-close:hover {
  background: rgb(155, 248, 112);
  color: black;
}
#lower-modal {
  clear: both;
  padding: 0 5px;
  margin-top: 10px;
}

@keyframes animatetop {
  from {
    top: -100%;
    left: 30%;
  }
  to {
    top: 15%;
    left: 30%;
  }
}
